
.custom-file-control[data-v-4]:lang(ru):before {
  content: "Выбрать";
}
.custom-file-control[data-v-4]:lang(ru):after {
  content: "Нужно фото...";
  position: absolute;
  left: 1em;
}
.custom-file-control.chosen[data-v-4]:lang(ru):after {
  content: "Есть фото!";
}
.custom-select[data-v-4] {
  line-height: 1.2em;
  cursor: pointer;
}
.wardrobe-items[data-v-4] {
  display: flex;
  flex-wrap: wrap;
}
.wardrobe-items .item[data-v-4] {
  position: relative;
  width: 10%;
}
.wardrobe-items .item:hover > .item-close[data-v-4] {
  opacity: 1;
}
.wardrobe-items .item:hover > .item-img[data-v-4] {
  opacity: 0.4;
}
.wardrobe-items .item-close[data-v-4] {
  opacity: 0;
  position: absolute;
  right: 10px;
  top: 10px;
  font-size: 2em;
  background: #d63d30;
  line-height: 0.6em;
  vertical-align: middle;
  text-align: center;
  padding: 4px;
  color: #fff;
  border: 0;
  border-radius: 4px;
  transition: opacity 0.2s ease;
}
.wardrobe-items .item-close[data-v-4]:hover {
  text-decoration: none;
}
.wardrobe-items .item-img[data-v-4] {
  width: 100%;
  transition: opacity 0.2s ease;
  display: block;
  position: relative;
}
.card-columns[data-v-4] {
  column-count: 6;
}
.card-columns .card[data-v-4] {
  break-inside: avoid;
}


